
'use client';

import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { Plus, Search, Edit, Trash2, Users, DollarSign, TrendingUp } from 'lucide-react';
import { LeaderFormSheet } from '@/components/dashboard/team-leaders/leader-form-sheet';
import { DeleteLeaderDialog } from '@/components/dashboard/team-leaders/delete-leader-dialog';
import { apiService } from '@/lib/api';
import { useToast } from '@/hooks/use-toast';

interface TeamLeader {
  team_leader_id: string;
  name: string;
  telegram_username: string;
  salary_type: 'Commission-based' | 'Fixed';
  commission_rate?: number;
  fixed_salary?: number;
  revenue_threshold?: number;
  status: 'Active' | 'Inactive';
  created_at?: string;
}

export default function TeamLeadersPage() {
  const [teamLeaders, setTeamLeaders] = useState<TeamLeader[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [salaryTypeFilter, setSalaryTypeFilter] = useState<string>('all');
  const [editingLeader, setEditingLeader] = useState<TeamLeader | null>(null);
  const [deletingLeader, setDeletingLeader] = useState<TeamLeader | null>(null);
  const [loading, setLoading] = useState(false);
  const { toast } = useToast();

  // Load team leaders on component mount
  useEffect(() => {
    loadTeamLeaders();
  }, []);

  const loadTeamLeaders = async () => {
    setLoading(true);
    try {
      const response = await apiService.getAllTeamLeaders();
      
      if (response.status === 'Success' && response.data) {
        setTeamLeaders(response.data);
        toast({
          title: "Team Leaders Loaded",
          description: `Successfully loaded ${response.data.length} team leaders.`,
        });
      } else {
        toast({
          title: "Error",
          description: response.message || "Failed to load team leaders",
          variant: "destructive",
        });
      }
    } catch (error) {
      console.error('Error loading team leaders:', error);
      toast({
        title: "Error",
        description: "Failed to load team leaders. Please try again.",
        variant: "destructive",
      });
    } finally {
      setLoading(false);
    }
  };

  const filteredLeaders = teamLeaders.filter(leader => {
    const matchesSearch = leader.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         leader.telegram_username.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         (leader.email && leader.email.toLowerCase().includes(searchTerm.toLowerCase()));
    const matchesStatus = statusFilter === 'all' || leader.status === statusFilter;
    const matchesSalaryType = salaryTypeFilter === 'all' || leader.salary_type === salaryTypeFilter;
    return matchesSearch && matchesStatus && matchesSalaryType;
  });

  const handleAddLeader = async (leaderData: any) => {
    try {
      const payload = {
        name: leaderData.name,
        telegram_username: leaderData.telegramUsername,
        status: leaderData.status,
        salary_type: leaderData.salaryType,
        revenue_threshold: leaderData.revenueThreshold,
        commission_rate: leaderData.commissionRate,
        fixed_salary: leaderData.fixedSalary
      };

      const response = await apiService.createTeamLeader(payload);
      
      if (response.status === 'Success') {
        toast({
          title: "Team Leader Added",
          description: `${leaderData.name} has been successfully added.`,
        });
        loadTeamLeaders(); // Reload the list
      } else {
        toast({
          title: "Error",
          description: response.message || "Failed to add team leader",
          variant: "destructive",
        });
      }
    } catch (error) {
      console.error('Error adding team leader:', error);
      toast({
        title: "Error",
        description: "Failed to add team leader. Please try again.",
        variant: "destructive",
      });
    }
    setEditingLeader(null);
  };

  const handleEditLeader = async (leaderData: any) => {
    if (!editingLeader) return;
    
    try {
      const payload = {
        name: leaderData.name,
        telegram_username: leaderData.telegramUsername,
        status: leaderData.status,
        salary_type: leaderData.salaryType,
        revenue_threshold: leaderData.revenueThreshold,
        commission_rate: leaderData.commissionRate,
        fixed_salary: leaderData.fixedSalary
      };

      const response = await apiService.updateTeamLeader(editingLeader.team_leader_id, payload);
      
      if (response.status === 'Success') {
        toast({
          title: "Team Leader Updated",
          description: `${leaderData.name} has been successfully updated.`,
        });
        loadTeamLeaders(); // Reload the list
      } else {
        toast({
          title: "Error",
          description: response.message || "Failed to update team leader",
          variant: "destructive",
        });
      }
    } catch (error) {
      console.error('Error updating team leader:', error);
      toast({
        title: "Error",
        description: "Failed to update team leader. Please try again.",
        variant: "destructive",
      });
    }
    
    setEditingLeader(null);
  };

  const handleDeleteLeader = async () => {
    if (!deletingLeader) return;
    
    try {
      const response = await apiService.deleteTeamLeader(deletingLeader.team_leader_id);
      
      if (response.status === 'Success') {
        toast({
          title: "Team Leader Deleted",
          description: `${deletingLeader.name} has been successfully deleted.`,
        });
        loadTeamLeaders(); // Reload the list
      } else {
        toast({
          title: "Error",
          description: response.message || "Failed to delete team leader",
          variant: "destructive",
        });
      }
    } catch (error) {
      console.error('Error deleting team leader:', error);
      toast({
        title: "Error",
        description: "Failed to delete team leader. Please try again.",
        variant: "destructive",
      });
    }
    
    setDeletingLeader(null);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Active': return 'bg-green-100 text-green-800';
      case 'Inactive': return 'bg-red-100 text-red-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const totalLeaders = teamLeaders.length;
  const activeLeaders = teamLeaders.filter(l => l.status === 'Active').length;
  const totalSalary = teamLeaders.reduce((sum, leader) => sum + (leader.fixed_salary || 0), 0);

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between">
        <div>
          <h1 className="text-3xl font-bold">Team Leaders</h1>
          <p className="text-muted-foreground">Manage team leaders and their salary structures</p>
        </div>
        <Button 
          onClick={() => setEditingLeader({
            team_leader_id: 'new',
            name: '',
            telegram_username: '',
            status: 'Active',
            salary_type: 'Fixed',
            revenue_threshold: 0,
            commission_rate: 0,
            fixed_salary: 0,
            created_at: new Date().toISOString()
          })} 
          className="mt-4 sm:mt-0"
        >
          <Plus className="mr-2 h-4 w-4" />
          Add Team Leader
        </Button>
      </div>

      {/* KPI Cards */}
      <div className="grid gap-4 md:grid-cols-3">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Leaders</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{totalLeaders}</div>
            <p className="text-xs text-muted-foreground">
              {activeLeaders} active
            </p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Salary</CardTitle>
            <DollarSign className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">${totalSalary.toLocaleString()}</div>
            <p className="text-xs text-muted-foreground">
              Combined payroll
            </p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Loading Status</CardTitle>
            <TrendingUp className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{loading ? 'Loading...' : 'Ready'}</div>
            <p className="text-xs text-muted-foreground">
              Data status
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Search and Filters */}
      <Card>
        <CardHeader>
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between space-y-2 sm:space-y-0">
            <CardTitle>Team Leaders List</CardTitle>
            <div className="flex flex-col sm:flex-row gap-2">
              <div className="relative">
                <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search leaders..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-8 w-full sm:w-64"
                />
              </div>
              <Select value={statusFilter} onValueChange={setStatusFilter}>
                <SelectTrigger className="w-full sm:w-32">
                  <SelectValue placeholder="Status" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All</SelectItem>
                  <SelectItem value="Active">Active</SelectItem>
                  <SelectItem value="Inactive">Inactive</SelectItem>
                </SelectContent>
              </Select>
              <Select value={salaryTypeFilter} onValueChange={setSalaryTypeFilter}>
                <SelectTrigger className="w-full sm:w-40">
                  <SelectValue placeholder="Salary Type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Types</SelectItem>
                  <SelectItem value="Commission-based">Commission</SelectItem>
                  <SelectItem value="Fixed">Fixed</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Name</TableHead>
                <TableHead>Telegram Username</TableHead>
                <TableHead>Salary Type</TableHead>
                <TableHead>Salary Details</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {loading ? (
                <TableRow>
                  <TableCell colSpan={5} className="text-center py-8">
                    Loading team leaders...
                  </TableCell>
                </TableRow>
              ) : filteredLeaders.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={5} className="text-center py-8">
                    No team leaders found
                  </TableCell>
                </TableRow>
              ) : (
                filteredLeaders.map((leader) => (
                  <TableRow key={leader.team_leader_id}>
                    <TableCell className="font-medium">{leader.name}</TableCell>
                    <TableCell>{leader.telegram_username}</TableCell>
                    <TableCell>
                      <Badge variant="outline">{leader.salary_type}</Badge>
                    </TableCell>
                    <TableCell>
                      <div className="text-sm">
                        {leader.salary_type === 'Commission-based' ? (
                          <>
                            <div>Threshold: ${leader.revenue_threshold?.toLocaleString()}</div>
                            <div>Rate: {leader.commission_rate}%</div>
                          </>
                        ) : (
                          <div className="font-medium">${leader.fixed_salary?.toLocaleString()}</div>
                        )}
                      </div>
                    </TableCell>
                    <TableCell>
                      <Badge className={getStatusColor(leader.status)}>
                        {leader.status}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      <div className="flex items-center space-x-2">
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => setEditingLeader(leader)}
                          title="Edit Leader"
                        >
                          <Edit className="h-4 w-4" />
                        </Button>
                        <Button
                          variant="ghost"
                          size="sm"
                          onClick={() => setDeletingLeader(leader)}
                          title="Delete Leader"
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </CardContent>
      </Card>

      {/* Team Leader Form */}
      {editingLeader && (
        <LeaderFormSheet
          isOpen={!!editingLeader}
          onClose={() => setEditingLeader(null)}
          leader={editingLeader.team_leader_id !== 'new' ? editingLeader : undefined}
          onSubmit={editingLeader.team_leader_id !== 'new' ? handleEditLeader : handleAddLeader}
        />
      )}

      {/* Delete Dialog */}
      {deletingLeader && (
        <DeleteLeaderDialog
          isOpen={!!deletingLeader}
          onClose={() => setDeletingLeader(null)}
          leader={deletingLeader}
          onConfirm={handleDeleteLeader}
        />
      )}
    </div>
  );
}
