'use client';
import React from 'react';
import { z } from 'zod';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';

import { Button } from '@/components/ui/button';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  Sheet,
  SheetContent,
  SheetHeader,
  SheetTitle,
  SheetDescription,
  SheetFooter,
  SheetClose,
} from '@/components/ui/sheet';

// Define the form schema specifically for team leaders
const teamLeaderFormSchema = z.object({
  name: z.string().min(2, 'Name must be at least 2 characters.'),
  telegramUsername: z.string().min(1, 'Telegram username is required.').startsWith('@', 'Username must start with @'),
  salaryType: z.enum(['Commission-based', 'Fixed']),
  commissionRate: z.number().min(0, 'Commission rate must be 0 or greater.').optional(),
  fixedSalary: z.number().min(0, 'Fixed salary must be 0 or greater.').optional(),
  revenueThreshold: z.number().min(0, 'Revenue threshold must be 0 or greater.').optional(),
  status: z.enum(['Active', 'Inactive']),
});

type TeamLeaderFormValues = z.infer<typeof teamLeaderFormSchema>;

interface TeamLeaderFormSheetProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (values: TeamLeaderFormValues) => void;
  leader?: any; // For editing existing leaders
}

export function LeaderFormSheet({
  isOpen,
  onClose,
  onSubmit,
  leader,
}: TeamLeaderFormSheetProps) {
  const form = useForm<TeamLeaderFormValues>({
    resolver: zodResolver(teamLeaderFormSchema),
    defaultValues: {
      name: leader?.name || '',
      telegramUsername: leader?.telegram_username || '',
      salaryType: leader?.salary_type || 'Fixed',
      commissionRate: leader?.commission_rate || undefined,
      fixedSalary: leader?.fixed_salary || undefined,
      revenueThreshold: leader?.revenue_threshold || undefined,
      status: leader?.status || 'Active',
    },
  });

  const salaryType = form.watch('salaryType');

  const title = leader ? 'Edit Team Leader' : 'Add New Team Leader';
  const description = leader
    ? 'Update the details for this team leader.'
    : 'Fill in the form to add a new team leader.';

  const handleSubmit = (values: TeamLeaderFormValues) => {


    console.log('🔵 Calling onSubmit with team leader data');
    
    // Ensure we're submitting team leader data with correct field mapping
    const teamLeaderData = {
      name: values.name,
      telegramUsername: values.telegramUsername, // Keep as telegramUsername for handler
      salaryType: values.salaryType, // Keep as salaryType for handler
      commissionRate: values.commissionRate, // Keep as commissionRate for handler
      fixedSalary: values.fixedSalary, // Keep as fixedSalary for handler
      revenueThreshold: values.revenueThreshold, // Keep as revenueThreshold for handler
      status: values.status,
    };
    

    onSubmit(teamLeaderData);
    onClose();
  };

  return (
    <Sheet open={isOpen} onOpenChange={onClose}>
      <SheetContent className="sm:max-w-lg flex flex-col">
        <SheetHeader>
          <SheetTitle>{title}</SheetTitle>
          <SheetDescription>{description}</SheetDescription>
        </SheetHeader>
        
        <Form {...form}>
          <form onSubmit={form.handleSubmit(handleSubmit)} className="space-y-6 flex-1">
            <div className="space-y-4">
              {/* Basic Information */}
              <FormField
                control={form.control}
                name="name"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Full Name *</FormLabel>
                    <FormControl>
                      <Input placeholder="John Doe" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="telegramUsername"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Telegram Username *</FormLabel>
                    <FormControl>
                      <Input placeholder="@johndoe" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />



              {/* Salary Configuration */}
              <FormField
                control={form.control}
                name="salaryType"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Salary Type *</FormLabel>
                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                      <FormControl>
                        <SelectTrigger>
                          <SelectValue placeholder="Select salary type" />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent>
                        <SelectItem value="Fixed">Fixed Salary</SelectItem>
                        <SelectItem value="Commission-based">Commission-based</SelectItem>
                      </SelectContent>
                    </Select>
                    <FormMessage />
                  </FormItem>
                )}
              />

              {/* Commission-based salary fields */}
              {salaryType === 'Commission-based' && (
                <>
                  <FormField
                    control={form.control}
                    name="revenueThreshold"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Revenue Threshold ($) *</FormLabel>
                        <FormControl>
                          <Input
                            type="number"
                            placeholder="10000"
                            {...field}
                            onChange={(e) => field.onChange(parseFloat(e.target.value) || undefined)}
                          />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />

                  <FormField
                    control={form.control}
                    name="commissionRate"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Commission Rate (%) *</FormLabel>
                        <FormControl>
                          <Input
                            type="number"
                            placeholder="5"
                            {...field}
                            onChange={(e) => field.onChange(parseFloat(e.target.value) || undefined)}
                          />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                </>
              )}

              {/* Fixed salary field */}
              {salaryType === 'Fixed' && (
                <FormField
                  control={form.control}
                  name="fixedSalary"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Fixed Salary ($) *</FormLabel>
                      <FormControl>
                        <Input 
                          type="number" 
                          placeholder="3000" 
                          {...field}
                          onChange={(e) => field.onChange(parseFloat(e.target.value) || undefined)}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              )}

              {/* Status */}
              <FormField
                control={form.control}
                name="status"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Status *</FormLabel>
                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                      <FormControl>
                        <SelectTrigger>
                          <SelectValue placeholder="Select status" />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent>
                        <SelectItem value="Active">Active</SelectItem>
                        <SelectItem value="Inactive">Inactive</SelectItem>
                      </SelectContent>
                    </Select>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            <SheetFooter className="mt-auto pt-6 pr-6">
              <SheetClose asChild>
                <Button type="button" variant="outline">
                  Cancel
                </Button>
              </SheetClose>
              <Button type="submit">
                {leader ? 'Update Team Leader' : 'Add Team Leader'}
              </Button>
            </SheetFooter>
          </form>
        </Form>
      </SheetContent>
    </Sheet>
  );
}
